.pop-up__title {
	font-family: Inter, Arial, sans-serif;
	color: black;
	font-style: normal;
	font-weight: 900;
	font-size: 24px;
	line-height: 29px;
	margin-top: 34px;
	margin-bottom: 50px;
	text-align: center;
}

@media screen and (max-width: 768px) {
	.pop-up__title {
		font-size: 18px;
		line-height: 22px;
		margin-bottom: 75px;
		text-align: center;
	}
}